.page.preLanding {
    width: 100vw;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    opacity: 1;
}

.page.preLanding h1 {
    color: var(--main-color);
}

.loading_container {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 500px;
}

.loading_container h1 {
    font-size: 80px;
    text-align: center;
}

.loading_bar_container {
    width: 100%;
    max-width: 500px;
    height: 15px;

    border: 1px solid var(--main-color);
}

.loading_bar {
    width: 100%;
    height: 15px;
    background-color: var(--main-color);
}

.preLanding_btn {
    margin-top: 20px;
    padding: 10px 50px;
    background-color: var(--main-color);
    color: #000;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
    .loading_container {
        padding: 10px;
        width: 100vw;
        transform: translate(-50%, -70%);
    }

    .loading_bar {
        width: 100%;
        height: 5px;
        background-color: var(--main-color);
    }    
    
    .loading_bar_container {
        height: 5px;
    }
}