@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
/* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap'); */
@font-face {
  font-family: belgan;
  src: url('./assets/fonts/belgan-aesthetic.regular.otf');
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: belgan;
  font-weight: 100;

  --main-color: #d0c870;
}

body {
  width: 100%;
  height: 100vh;

  color: var(--main-color);
  background-color: #000;
  /* overflow: hidden; */
}

.page {
  position: fixed;
  width: 100vw;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
}

@media only screen and (max-width: 500px) {
  * {
    cursor: default
  }

  body {
    height: 102vh;
  }

  .page {
    position: fixed;
    width: 100vw;
    height: 101vh;
  }
}

