.page.me {
    display: flex;
    opacity: 0;
}
.column {
    padding: 40px;
    padding-right: 60px;
    margin-left: auto;
    width: 50vw;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 10;
}

.column h1 {
    margin-bottom: 15px;
}

.me_title {
    text-shadow: 0px 0px 10px var(--main-color);
}

.column p {
    font-weight: 100;
    letter-spacing: 1px;
}

@media only screen and (max-width: 500px) {
    .column {
        padding: 20px;
        width: 100vw;
        justify-content: flex-end;
    }
    .column p {
        margin-bottom: 10px;
        font-size: 14px;
        text-align: start;
    }
}

@media only screen and (max-width: 300px) {
    .column p {
        margin-bottom: 0;
        font-size: 12px;
    }
}