.skills_title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    text-shadow: 0px 0px 10px var(--main-color);
    font-size: 70px;
}

.row {
    width: 100vw;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.column_3 {
    width: 30vw;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.column_3 ul {
    width: 100%;
    list-style: none;
}

.column_3 ul li {
    margin-bottom: 5px;
    text-align: center;
    font-family: roboto;
    font-weight: 300;
}

@media only screen and (max-width: 500px) {
    .column_3 {
        margin: 20px;
        width: auto;
    }
    .row {    
        margin-top: 92px;
        align-items: flex-start;
        justify-content: center;
        flex-wrap: wrap;
    }
}

@media only screen and (max-width: 300px) {
    .column_3 {
        margin: 8px;
    }
    .column_3 h1 {
        margin-top: 15px;
    }
    .column_3 ul li {
        font-size: 14px;
    }
    /* .row {
        align-items: flex-start;
        justify-content: center;
        flex-wrap: wrap;
    } */
}