.page.landing {
    display: flex;
    opacity: 1;
    /* z-index: 1080; */
}

.video {
    position: absolute;
    top: 0;
    left: 0;
    
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
}

.hi {
    width: 100%;
    max-width: 500px;
    padding: 20px;

    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translate(-50%, 0%);

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.hi_top {
    width: 100%;
    max-width: 380px;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.hi_top .border {
    width: 100%;
    max-width: 130px;
    height: 1px;

    background-color: var(--main-color);
    border-left: none;
    border-right: none;
}

.hi_top h2 {
    padding: 0 10px;
    font-size: 30px;
}

.hi h1 {
    font-size: 70px;
    font-weight: 100;
    line-height: 60px;

    color: var(--main-color);
    text-shadow: 0px 0px 10px var(--main-color);
    /* animation: shadowAnimation 5s ease-in-out infinite; */
}

