        text-shadow: 0px 0px 10px var(--main-color);
    }
    100% {
        text-shadow: 0px 0px 0px var(--main-color);
    }
} */

.social_media {
    position: absolute;
    left: 30px;
    top: 50%;
    transform: translate(0%, -50%);

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    /* z-index: 1080; */
}

.social_media a {
    padding: 5px;
    margin-bottom: 20px;

    /* font-family: 'Roboto', sans-serif; */
    font-weight: 300;
    font-size: 15px;
    
    color: var(--main-color);
    text-decoration: none;
    cursor: pointer;
}

.social_media a span {
    font-family: belgan;
    font-weight: 300;
}

.social_media a:nth-last-child(1) {
    margin-bottom: 0;
}

.social_media a:hover > span {
    font-weight: 500;
}


@media only screen and (max-width: 500px) {
    .hi {
        bottom: 45px;
    }
    .hi h1 {
        text-align: center;
        font-size: 48px;
    }
    .hi_top {
        justify-content: center;
    }
    .hi_top .border {
        max-width: 75px;
    }
    .hi_top h2 {
       font-size: 21px;
    }
}
@media only screen and (max-width: 300px) {
    .hi h1 {
        text-align: center;
        font-size: 39px;
    }
    .social_media {
        left: 10px;
    }
    .components {
        right: 5px;
    }
}