.home {
    width: 100vw;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    
    background-color: #000;
    color: var(--main-color);
    overflow: hidden;
}

.vector {
    width: 70%;
    
    position: fixed;
    top: 30px;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2000;
}

.logo {
    width: 40px;
    
    position: fixed;
    top: 30px;
    left: 50%;
    
    transform: translate(-50%, -50%);
    z-index: 1090;
}

.components {
    padding: 20px;
    position: fixed;
    right: 20px;

    display: flex;
    flex-direction: column;
    z-index: 1090;
}

.dot {
    margin-bottom: 20px;
    width: 5px;
    height: 5px;

    border-radius: 50%;
    background-color: var(--main-color);
    cursor: pointer;
}

.dot:nth-last-child(1) {
    margin-bottom: 0;
}

.btns {
    padding: 20px;
    position: fixed;
    bottom: 0;
    right: 0;

    display: flex;
    flex-direction: column;
    z-index: 2000;
}

.btns .btn {
    width: 50px;
    height: 50px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

.btns .btn:hover {
    background-color: #d0c87030;
}

.btns .btn img {
    height: 30px;
}

.btn.prev {
    transform: rotate(180deg);
    border-top: none;
}

.home_border {
    position: fixed;
    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    border: 1px solid var(--main-color);
    z-index: 1030;
    pointer-events: none;
}

@media only screen and (max-width: 500px) {
    .vector {
        width: 90%;
    }
    .components {
        right: 5px;
    }
    .social_media {
        left: 15px;
    }
}