.present_my_self_title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    text-shadow: 0px 0px 10px var(--main-color);
    text-align: center;
    font-size: 70px;
    line-height: 70px;
}

.present_my_self_video_container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    width: 75vh;
    height: 75vh;

    display: flex;
    align-items: center;
    justify-content: center;

    overflow: hidden;
}

.present_my_self_video {
    width: 100%;
}

.video_frame {
    transform: rotate(0deg);
    height: 63vh;
}


@media only screen and (max-width: 500px) {
    .present_my_self_title {
        font-size: 27px;
        /* line-height: 33px; */
    }
    .present_my_self_video_container {
        overflow: unset;
        width: 90vw;
        transform: translate(-50%, -50%) rotate(90deg);
    }
    .video_frame {
        height: auto;
        width: 132vw;
        transform: rotate(90deg);
    }
    .present_my_self_video {
        width: 110%;
    }
}

@media only screen and (max-width: 300px) {
    .present_my_self_title {
        font-size: 21px;
    }
}