.page.projects {
    z-index: 1070;
}

.projects_title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    text-shadow: 0px 0px 10px var(--main-color);
    font-size: 70px;
    opacity: 0;
}

.projects_container {
    position: absolute;
    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    overflow: hidden;
}

.single_project_container {
    position: absolute;
    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: .4s ease-in-out;
}

.video_container {
    position: relative;
    width: 50vw;
    padding: 0 40px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.column_2 {
    padding: 40px;
    padding-right: 60px;
    margin-left: auto;
    width: 50vw;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 1070;
}

.video_container {
    opacity: 0;
}
.video_container img {
    position: absolute;
    width: 80%;
}
.video_container video {
    position: absolute;
    width: 61%;
}
.column_2 h1 {
    opacity: 0;
    pointer-events: none;
}
.column_2 p {
    opacity: 0;
    pointer-events: none;
}
.explore_btn {
    padding: 10px 30px;
    margin-top: 20px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: var(--main-color);
    color: #000;
    text-decoration: none;
    font-weight: bold;
}

.prevProj, .nextProj {
    position: absolute;
    top: 50%;
    transform: translate(0%, -50%);
    
    width: 50px;
    height: 50px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: transparent;    
    border: none;
    outline: none;
    cursor: pointer;

    z-index: 1070;
}

.prevProj:hover, .nextProj:hover {
    transition: .4s;
    background-color: #d0c86f50;
}
.prevProj {
    left: 0;
}
.nextProj {
    right: 0;
}

.prevProj img, .nextProj img {
    height: 30px;
}

.prevProj img {
    transform: rotate(90deg);
}
.nextProj img {
    transform: rotate(-90deg);
}

@media only screen and (max-width: 500px) {
    .single_project_container {
        flex-direction: column;
    }
    .video_container {
        margin-top: 70px;
        height: 30vh;
        width: 100vw;
    }
    .column_2 {
        padding: 20px;
        margin-left: 0;
        width: 100vw;
        height: auto;
    }
    .column_2 p {
        font-size: 14px;
    }

    /* .projects_title {
        transform: translate(-50%, -50%);
    } */
}