.Contact {
    pointer-events: none;
    z-index: 1070;
}
.contact_title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    text-shadow: 0px 0px 10px var(--main-color);
    font-size: 70px;    
}

.contact_container {
    width: 100vw;
}

.contact_container {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.my_photo {
    width: 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my_photo img {
    width: 85%;
}

.form {
    padding: 40px;
    width: 50vw;

    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
}

.form label {
    margin: 10px 0;
}

.form input, .form textarea {
    padding: 10px;
    margin-bottom: 10px;
    width: 100%;

    color: var(--main-color);
    background-color: transparent;
    border: 1px solid var(--main-color);
    outline: none;
}

.form input::placeholder, .form textarea::placeholder {
    color: var(--main-color);
}

.form input:autofil, .form textarea:autofil,
.form input:-webkit-autofil, .form textarea::-webkit-autofil {
    background-color: transparent;
}


.send_btn {
    margin-top: 10px;
    padding: 10px;
    width: 100%;

    background-color: var(--main-color);
    outline: none;
    border: 1px solid var(--main-color);
    cursor: pointer;
    transition: .4s;
}

.send_btn:hover {
    background-color: #000;
    border: 1px solid var(--main-color);
    color: var(--main-color);
}

@media only screen and (max-width: 500px) {
    .contact_container {
        flex-direction: column;
    }
    .my_photo {
        display: none;
    }
    .form {
        padding: 20px;
        padding-top: 0;
        width: 100vw;
    }
}

